.container {
  position: relative;
  width: 100%;
  max-width: 960px;
  margin: 0 auto;
  padding: 0 16px;
  box-sizing: border-box;
}
.column,
.columns {
  width: 100%;
  float: left;
  box-sizing: border-box;
}
@media (min-width: 400px) {
.container {
    width: 85%;
    padding: 0;
}
}
@media (min-width: 550px) {
.container {
    width: 80%;
}
.column,
  .columns {
    margin-left: 4%;
}
.column:first-child,
  .columns:first-child {
    margin-left: 0;
}
.one.column,
