}
.twelve.columns {
    width: 100%;
    margin-left: 0;
}
.one-third.column {
    width: 30.66667%;
}
.two-thirds.column {
    width: 65.33333%;
}
.one-half.column {
    width: 48%;
}
.offset-by-one.column,
  .offset-by-one.columns {
    margin-left: 8.66667%;
}
.offset-by-two.column,
