}
.offset-by-two-thirds.column,
  .offset-by-two-thirds.columns {
    margin-left: 69.33333%;
}
.offset-by-one-half.column,
  .offset-by-one-half.column {
    margin-left: 52%;
}
}
.container:after,
.row:after,
.u-cf {
  content: "";
  display: table;
  clear: both;
}
.button,
button {
  display: inline-block;
  height: 38px;
  padding: 0 30px;
  color: #009FB8;
  text-align: center;
  font-size: 11px;
  font-weight: 600;
  line-height: 38px;
  letter-spacing: .1rem;
  text-transform: uppercase;
  text-decoration: none;
  white-space: nowrap;
  background-color: transparent;
  border-radius: 0;
  border: 1px solid #6F6F6F;
  cursor: pointer;
  box-sizing: border-box;
}
input[type="submit"], input[type="reset"], input[type="button"] {
  display: inline-block;
  height: 38px;
  padding: 0 30px;
  color: #009FB8;
  text-align: center;
  font-size: 11px;
  font-weight: 600;
  line-height: 38px;
  letter-spacing: .1rem;
  text-transform: uppercase;
  text-decoration: none;
