
.navbar {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 1030;
  padding-top: 1em;
}
.logo-min {
  float: left;
  margin-top: 0.5em;
  margin-right: -1px;
}
.logo-min path {
    fill: #050505;
}
ul.navbt {
  list-style-type: none;
  padding: 0;
  float: right;
}
ul.navbt li {
    display: inline-block;
    margin: 0 0 0 1rem;
}
.button-square {
  color: #050505;
  font-size: 1em;
  margin: 0;
  padding: 0;
  border: 0.5px solid #050505;
  height: 3.5em;
  width: 3.5em;
  line-height: 1.5em;
  position: relative;
}
.button-square * {
    height: 1.5em;
    width: 1.5em;
    display: block;
    position: absolute;
    top: 50%;
    margin-top: -0.75em;
    margin-left: -0.75em;
    left: 50%;
}
.button-square * path {
      fill: #050505;
}
.button-square:hover {
    border-color: #10CEDA;
}
.button-square.noborder {
    border-color: transparent;
    border-radius: 1.5em;
}



